<template>
    <div>
        <div class="guid_title">目录</div>
        <div v-for='(item,index) in guideList' :key='index' @click='skipPage(item)' class="index_item">
            {{`(${index+1})................${item.name}....................`}}
        </div>
    </div>
</template>
<script>
export default {
    name:'guideList',
    data() {
        return {
            guideList:[{'name':'多段烹饪','link':'/lineViewsFromRokiApp'},{'name':'中国新厨房抽奖','link':'/lotteryDraw'},{'name':'618抽奖活动',link:'/juneLottery'}]
        }
    },
    created(){
        //console.log('5555')
    },
    mounted(){
        //console.log('6666')
    },
    methods:{
        skipPage(item){
            if(item.name=='多段烹饪'){         
                this.$router.push({'path':item.link,'query':{'avatar': 'http://roki-test.oss-cn-qingdao.aliyuncs.com//avatar/default_avatar.png','name':'小小酥','id':29}})
            }else{
                this.$router.push(item.link);
            }
             

        },
    }
}
</script>
<style lang="scss" scoped>
.guid_title{
    font-size:36px;
    padding:20px;
}
.index_item{
    padding:20px;
    text-align:left;
}
</style>



